
.applicationIn .tab_type_box{
  width: 100%;
    box-sizing: border-box
}
.applicationIn .tab_type_head a{
  width: 184px;
}
.applicationIn_content{
  padding-top: 30px;
  padding-left: 300px;
    font-size: 14px;
}
.applicationIn_content section{
  display: flex;
  height: 70px;
  justify-content: flex-start;
  align-items: center;
}
.applicationIn_content section.top{
  align-items: flex-start;
  padding: 10px 0;
  height: 100px;
}
.applicationIn_content section.top img{
  width: 100px;
  height: 100px;
}
.applicationIn_content section.top .lable{
  padding-top: 20px;
}
.applicationIn_content section .lable{
  width: 135px;
  color: #616161;
}
.applicationIn_content section .types{
  height: 40px;
    padding: 0 20px;
  line-height: 40px;
  text-align: center;
  margin-right: 10px;
  border: 1px solid #e5e7ed;
  background: #fff;
  color: #898989;
  cursor: pointer;
    transition: .3s;
}
.applicationIn_content section .types:hover{
       color: #006EFF
}
.applicationIn_content section .types.active{
  border: 1px solid #0066eb;
  background: #006EFF;
  color: #fff;
}
.applicationIn_content section input{
  width: 455px;
  height: 40px;
  padding-left: 10px;
  border: 1px solid #e5e7ed;
    font-size: 14px;
}
.applicationIn_content section select{
  width: 175px;
  height: 40px;
  border: 1px solid #e5e6ed;
  margin-right: 10px;
    font-size: 14px;
}
.btn_box{
  display: flex;
  justify-content: center;
  padding-bottom: 20px;
}
.btn_box span{
  width: 158px;
  height: 58px;
  margin: 10px;
}
.business-license-list{
       height: auto!important;
           align-items: end!important
}
.business-license-img{
       width: 400px!important;
       height: auto!important;
       margin-bottom: 30px;
}
.applicationIn_content .top{
       background: none!important;
}
.mt15{
       margin-top: 15px
}
.upload-box{
     position: relative;
}
#file{
     position: absolute;
     left: 136px;
     opacity: 0;
}




