
.bodytabs {
  width: 100%;
  height: 398px;
  border-top: 3px solid;
  background: #ffffff;
  margin-top: 20px;
}
.bodyTabSidebar {
  float: left;
  width: 208px;
  height: 368px;
  padding-top: 30px;
  padding-left: 20px;
  color: #ffffff;
}
.bodyTabSidebar p {
  font-size: 22px;
  font-weight: bold;
}
.bodyTabSidebar .siderLine {
  display: block;
  width: 88px;
  height: 0px;
  border-top: 3px solid #ffffff;
  margin-top: 5px;
  margin-bottom: 15px;
}
.bodyTabSidebar .siderDes {
  font-size: 14px;
}
.bodyTabSidebar ul {
  float: right;
  margin-top: 50px;
}
.bodyTabSidebar ul li {
  height: 38px;
  line-height: 38px;
  width: 140px;
  text-align: right;
  padding-right: 20px;
    font-size: 14px;
  margin-bottom: 10px;
    background: rgba(0,0,0,.12)!important;
    transition: .3s;
    cursor: pointer
}
.bodyTabSidebar ul li:hover{
    background: rgba(0,0,0,.07)!important;
}
.bodyTabSidebar ul li.active {
  background: #ffffff !important;
}
.bodyTabCentent {
  float: left;
  width: 982px;
}
.bodyTabCentent section {
  float: left;
  display: flex;
  width: 490px;
  height: 132px;
  border-bottom: 1px solid #f0f0ff;
  justify-content: left;
  align-items: center;
    position: relative;
    z-index: 1;
    transition: .3s;
/*    top: 0;*/
}
.bodyTabCentent section:hover{
/*        top: -2px;*/
        z-index: 2;
        box-shadow: 0 2px 2px rgba(0,0,0,.04)
}
.bodyTabCentent section div p:nth-child(1):hover{
        color:#0585FF
}
.bodyTabCentent section:nth-child(2n + 1) {
  border-right: 1px solid #f0f0f0;
}
.bodyTabCentent section img {
  width: 88px;
  height: 88px;
  border-radius: 100%;
  margin-left: 30px;
  margin-right: 20px;
    border: 1px solid #EDEDED
}
.bodyTabCentent section div p:nth-child(1) {
  font-size: 16px;
  color: #0b0b0b;
  line-height: 30px;
    transition: .3s;
    cursor: pointer
}
.bodyTabCentent section div p:nth-child(2) span {
  padding: 0 10px;
  line-height: 24px;
  font-size: 12px;
  color: #ccc;
  border: 1px solid #e1e1e1;
  margin-right: 5px;
}
.bodyTabCentent section div p:nth-child(3) {
  line-height: 24px;
  font-size: 12px;
  color: #b3b3b3;
}
.bodyTabCentent section div p:nth-child(3) span {
  color: #ffc373;
  font-size: 14px;
}
.bodyTabCentent section div p:nth-child(3) i {
  color: #898989;
  font-size: 14px;
}
.bodyTabCentent .img-logo-wrap{
        margin: 0 20px;
        margin-left: 30px
}




