.select-type-button{
  height: 40px;
  padding: 0 20px;
  line-height: 40px;
  text-align: center;
  margin-right: 10px;
  border: 1px solid #e5e7ed;
  background: #fff;
  color: #898989;
  cursor: pointer;
  transition: .3s;
  border-radius: 0;
}
.select-type-button.active {
  border: 1px solid #0066eb;
  background: #006EFF;
  color: #fff;
}
.company_submit{
  flex-basis: 100px;
}
.invoice_cancel_btn{
  flex-basis: 100px;
}



