
.company_logo_box{
  width: 224px;
  height: 218px;
  border: 1px solid #e5e7ed;
  background: #f9fafc;
  text-align: center;
  margin-bottom: 30px;
}
.company_logo_box section{
  width: 128px;
  height: 128px;
  position: relative;
  margin: auto;
  margin-top: 18px;
}
.company_logo_box section img{
  width: 100%;
  height: 100%;
}
.company_logo_box section span{
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  height: 25px;
  line-height: 25px;
  color: #fff;
  background: rgba(0, 0, 0, 0.4);
}
.company_logo_box p{
  width: 130px;
  margin: auto;
  font-size: 13px;
  color: #898989;
  line-height: 20px;
  padding-top: 10px;
}



