
.invoice_table{
  color: #898989;
  font-size: 14px;
}
.invoice_table th{
  height: 40px;
  background: #f9fafc;
  border: 1px solid #e5e7ed;
  padding-left: 20px;
  vertical-align: middle;
  text-align: left;
}
.invoice_table th.text_center{
  text-align: center;
  padding: 0;
}
.invoice_table th.text_left{
  text-align: left;
  padding-left: 20px;
}
.invoice_table td{
  height: 50px;
  border: 1px solid #e5e7ed;
  vertical-align: middle;
  text-align: center;
}
.invoice_table td.money{
  color: #ff8a6e;
}
.invoice_table td a.apply_invoice{
  color: #87ce76;
  padding: 5px 10px;
  border: 1px solid #87ce76;
  border-radius: 4px;
  font-size: 12px;
}
.invoice_table td a.invoice_details{
  color: #6389ed;
}



