
.company .tab_type_box{
  border-bottom: 0;
}
.company_selet_box{
  width: 962px;
  border: 1px solid #e5e7ed;
  border-top: 0;
  padding: 20px 0;
}
.company_selet_box section{
  display: flex;
  height: 36px;
  justify-content: flex-start;
  align-items: center;
  padding-left: 20px;
  color: #898989;
  font-size: 14px;

  flex-wrap: wrap;
}
.company_selet_box section p{
  width: 90px;
}
.company_selet_box section span{
  height: 22px;
  line-height: 22px;
  padding: 0 9px;
  cursor: pointer;
    transition: .3s;
}
.company_selet_box section span:hover{
      color: #0484FF
}
.company_selet_box section span.active{
  border-radius: 4px;
  background: #0484FF;
  color: #fff;
}
.company_list_box{
  margin-top: 30px;
}
.company_list_head span{
  color: #ff8a6e;
}



