
.order_form{
    border: 1px solid #e5e7ed;
    margin-bottom: 20px;
}
.order_form section:nth-child(1){
    display: flex;
    height: 31px;
    justify-content: flex-start;
    align-items: center;
    padding-left: 20px;
    background: #f9fafc;
    border-bottom: 1px solid #e5e7ed;
    font-size: 10px;
}
.order_form .money{
  color:#ff8a6e;
}
.order_form .order_form_div{
  display: flex;
  height: 70px;
  padding: 0 20px;
  justify-content: space-between;
  align-items: center;
  color: #616161;
}
.order_form .change_pay{
  padding: 5px 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
}
.manageOrderInforTitle{
  padding: 20px 0;
  font-size: 13px;
  font-weight: 600;
  color: #616161;
}
.manageOrderInforBox{
  padding: 20px 0;
  margin-bottom: 20px;
  border-top: 1px solid #e5e7ed;
  border-bottom: 1px solid #e5e7ed;
  color: #616161;
}
.payWay{
  display: block;
  width: 158px;
  height: 70px;
  border: 1px solid #e5e7ed;
  background-repeat: no-repeat;
  background-position: center;
}
.payWay_alpay{
  background-image: url(image/alpay.png);
}
.manageOrderInforBox section{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.manageOrderInforBox section.top{
  align-items: flex-start;
}
.manageOrderInforBox section div{
  width: 80px;
  text-align: center;
}
.manageOrderInforBox section div span{
  display: block;
}
.manageOrderInforBox section div span:nth-child(1){
  padding: 15px 0;
  color: #616161;
}
.manageOrderInforBox section div span.btn{
  width: 70px;
  height: 25px;
  line-height: 25px;
  border-radius: 4px;
  border: 1px solid #e5e7ed;
  margin: auto;
}
.manageOrderInforBox_b{
  display: flex;
  justify-content: space-between;
}
.Speed{
  width: 78px;
  height: 78px;
  border: 1px dashed #ccc;
  border-radius: 100%;
  background: #f8f8f8;
  font-size: 32px;
  text-align: center;
  line-height: 78px;
  color: #b3b3b3;
}
.Speed.active{
  color: #87ce76;
  border: 1px dashed #87ce76;
}
.line{
  flex-grow: 1;
  height: 0px;
  border-top: 1px dashed #ccc;
}



