
.indexLoginWrapper {
  position: absolute;
  width: 1210px;
  height: 408px;
  top: 0;
  left: 50%;
  margin-left: -605px;
}
.indexLogin {
  position: absolute;
  right: 0;
  top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  width: 226px;
  height: 381px;
  background: #ffffff;
  z-index: 10;
}
.indexLogin .userIcon {
  width: 70px;
  height: 70px;
  border-radius: 100%;
}
.indexLogin .userName {
  font-size: 14px;
  color: #b3b3b3;
  padding: 10px 0;
}
.indexLogin .btns {
  display: flex;
  height: 36px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.indexLogin .btns  a{
  padding: 0;
}
.indexLogin .btns .userBtn {
  display: block;
  width: 90px;
  height: 34px;
  border-radius: 34px;
  cursor: pointer;
  text-align: center;
  line-height: 34px;
  font-size: 13px;
  margin: 0 5px;
}
.indexLogin .btns .loginBtn {
  border: 1px solid #337ffb;
  color: #337ffb;
    transition: .3s;
    cursor: pointer
}
.indexLogin .btns .loginBtn:hover{
        background: #337ffb;
        color: #fff
}
.indexLogin .btns .registerBtn {
  border: 1px solid #ffbe61;
  color: #ffbe61;
    transition: .3s;
    cursor: pointer
}
.indexLogin .btns .registerBtn:hover{
        background: #ffbe61;
        color: #fff
}
.indexLogin .introduceLine {
  display: block;
  margin-left: 80px;
  border-top: 1px solid #e6e6e6;
}
.indexLogin .introduceDiv {
  width: 100%;
  border-top: 1px solid #e6e6e6;
  margin-top: 30px;
}
.indexLogin .introduceItem {
  display: flex;
  width: 100%;
  height: 90px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.indexLogin .introduceItem .introduceIcon {
  width: 40px;
  margin-right: 15px;
}
.indexLogin .introduceItem p {
  display: flex;
  width: 120px;
  flex-direction: column;
  justify-content: center;
}
.indexLogin .introduceItem p span:nth-child(1) {
  font-size: 14px;
  color: #898989;
}
.indexLogin .introduceItem p span:nth-child(2) {
  font-size: 20px;
  color: #616161;
    font-family: alial;
    font-weight: bold;
    margin-top: 6px
}



