
.help_select{
  display: flex;
  width: 550px;
  height: 32px;
  border: 2px solid #0079FF;
  margin: 30px auto 35px auto;
}
.help_select input{
  flex-grow: 1;
  border: 0;
  padding-left: 10px;
}
.help_select_btn{
  width: 80px;
  height: 32px;
  background: #0079FF;
  color: #fff;
  font-size: 15px;
  text-align: center;
  line-height: 32px;
    transition: .3s;
    cursor: pointer
}
.help_select_btn:hover{
      background: #228bff
}
.helpCentre ul{
  width: 925px;
  margin: auto;
}
.helpCentre ul li{
  width: 925px;
  height: 48px;
  line-height: 48px;
  border-bottom: 1px dashed #f1f1f1;
}
.helpCentre ul li a{
  display: block;
  padding-left: 30px;
  color: #848484;
  font-size: 15px;
    transition: .3s;
}
.helpCentre ul li a:hover{
      color: #0079FF
}



