
.bannerBox{
  position: absolute;
  width: 100%;
  left: 0;
  min-width: 1210px;
}
.swiper-container {
  width: 100%;
  height: 408px;
}
.swiper-wrapper {
  width: 100%;
  height: 100%;
}
.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100%;
}
.swiper-slide img {
  width: 100%;
  height: 100%;
}
    
/*    banner Nav样式*/
.swiper-container.swiper-container-horizontal{
        text-align: center
}
.swiper-pagination.swiper-pagination-bullets{
        display: inline-block!important;
        width: auto;
        left: 50%;
        background: rgba(0,0,0,.4);
        transform:translate(-50%,0);
        height: 24px;
        line-height: 24px;
        border-radius: 24px;
        padding: 0 4px
}
.swiper-pagination-bullet{
        width: 12px;
        height: 12px;
        border-radius: 12px;
        margin: 6px 3px!important;
        background: #fff;
        opacity: .3
}
.swiper-pagination-bullet-active{
        opacity: 1
}




