
.indexNews {
  float: right;
  width: 590px;
  height: 304px;
  border: 1px solid #efefef;
}
.indexNews p {
  display: flex;
  width: 100%;
  height: 40px;
  border-bottom: 1px solid #efefef;
  justify-content: space-between;
  align-items: center;
  background: #fcfcfc;
}
.indexNews p span {
  padding-left: 20px;
  font-size: 14px;
  font-weight: bold;
  color: #4a4a4a;
}
.indexNews a {
  padding-right: 20px;
  color: #4A4A4A;
  cursor: pointer;
    transition: .3s;
}
.indexNews a:hover{
        color: #0585FF
}
.indexNews ul {
  width: 530px;
  margin: auto;
  margin-top: 10px;
  list-style: none;
  counter-reset: count;
}
.indexNews ul li {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 48px;
  border-bottom: 1px dashed #e5e7ed;
}
.indexNews ul li:last-child {
  border: 0;
}
.indexNews ul li:before {
  content: "● ";
  color: #b3b3b3;
}
.indexNews li span:nth-child(1) {
  font-size: 14px;
  color: #565656;
  padding: 0 10px;
    cursor: pointer;
    transition: .3s;
  flex-grow: 2;
}
.indexNews li span:nth-child(1):hover{
        color:#0585FF
}
.indexNews li span:nth-child(2) {
  font-size: 14px;
  color: #b3b3b3;
}



