
.wallet{
  display: flex;
  height: 100px;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px 0 20px;
  border: 1px solid #e5e7ed;
  background: #f9fafc;
}
.wallet .icon-wallet{
  width: 60px;
  height: 60px;
  font-size: 32px;
  color: #fff;
  text-align: center;
  align-items: center;
  line-height: 60px;
  background: #87ce76;
  border-radius: 100%;
}
.wallet section{
  flex-grow: 1;
  padding-left: 20px;
}
.wallet section p:nth-child(1){
  font-size: 14px;
  line-height: 27px;
  color: #616161;
}
.wallet section p:nth-child(2){
  font-size: 17px;
  color: #87ce76;
  font-weight: 600;
}
.wallet .withdraw_btn{
  width: 158px;
  height: 44px;
  text-align: center;
  line-height: 44px;
  font-size: 14px;
  border: 1px solid #6389ed;
  border-radius: 4px;
  color: #6389ed;
}
.wallet_table_title{
  padding-top: 30px;
  padding-bottom: 20px;
  font-size: 14px;
  color: #616161;
}
.wallet_table .income{
  color: #87ce76;
}
.wallet_table .expend{
  color: #ff8a6e;
}



