
.com-serve-list>li{
    padding: 20px;
    background: #F8F9FC;
    border: 1px solid  #E1E3EA;
    color: #616161;
    line-height: 20px;
    position: relative;
    margin-bottom: 15px
}
.com-serve-l{
    color: #898989;
    font-size: 14px
}
.com-serve-l>h3{
    font-size: 16px;
    font-weight: bold;
    color: #616161;
    margin-bottom: 6px
}
.com-serve-l>div>span{
    color: #FF8A6E
}
.com-serve-l>p>span{
    color: #B3B3B3
}
.com-serve-btn{
    position: absolute;
    right: 20px;
    top: 20px;
}



