
.newBodys {
  float: left;
  width: 590px;
  height: 304px;
  border: 1px solid #efefef;
  border-top: 0;
}
.newBodys ul {
  display: flex;
  width: 100%;
  height: 40px;
  justify-content: center;
  align-items: center;
}
.newBodys ul li {
  display: flex;
  flex-grow: 1;
  height: 40px;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: bold;
  background: #fcfcfc;
  border: 1px solid #efefef;
  border-left: 0;
  color: #4a4a4a;
    cursor: pointer;
    transition: .3s;
}
.newBodys ul li:after{
        content: "";
        position: absolute;
        left: -1px;
        right: -1px;
        top: -2px;
        height: 0px;
        background: #337ffb;
/*        transition: .3s*/
}
.newBodys ul li:hover{
        color: #0585FF
}
.newBodys ul li:nth-child(3) {
  border-right: 0;
}
.newBodys ul li.active {
/*  height: 38px;*/
/*  border-top: 3px solid #337ffb;*/
  border-bottom-color: #fff;
  color: #337ffb;
  background: #ffffff;
    transition: inherit;
    position: relative;
}
.newBodys ul li.active:after{
        height: 3px;
}
.newBodyItem {
  float: left;
  display: flex;
  width: 270px;
  height: 100px;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid #efefef;
  margin-left: 20px;
  margin-top: 20px;
    transition: .3s;
    position: relative;
    top: 0
}
.newBodyItem:hover{
        top: -2px;
        box-shadow: 0 3px 3px rgba(0,0,0,.06)
}
.newBodyItem:nth-child(2n + 1) {
  margin-left: 10px;
}
.newBodyItem img {
  width: 70px;
  height: 70px;
  margin: 15px;
  border-radius: 100%;
}
.newBodyItem p:nth-child(1) {
  font-size: 14px;
  color: #0b0b0b;
  line-height: 30px;
}
.newBodyItem p:nth-child(2) span {
  padding: 0 5px;
  line-height: 24px;
  font-size: 12px;
  color: #ccc;
  border: 1px solid #e1e1e1;
  margin-right: 5px;
}



