
.invoice_table .money{
  color: #ff8a6e;
}
.manage_list_common.manage_list_invoice li p:nth-child(1){
  padding-left: 35px;
  width: 175px;
}
.title_invoice{
  padding-top: 40px;
  padding-bottom: 25px;
  border-bottom: 1px solid #e5e6ed;
  color: #616161;
  font-size: 14px;
  font-weight: 600;
}
.manage_list_invoice li span:nth-child(2){
  color: #898989;
}
.consignee_address{
  display: flex;
  height: 70px;
  justify-content: flex-start;
  align-items: center;
  padding-left: 20px;
  border-bottom: 1px solid #e5e6ed;
}
.consignee_address span{
  padding-right: 10px;
  color: #898989;
  font-size: 14px;
}
.consignee_address span:nth-child(1){
  font-size: 15px;
}
.consignee_address span:nth-child(2){
  font-weight: 600;
}



