
.news_details_title{
  font-size: 24px;
  color: #616161;
  line-height: 32px;
}
.news_details_subhead{
  display: flex;
  height: 22px;
  justify-content: space-between;
  align-items: center;
}
.news_details_subhead{
  color: #d6d6d6;
}
.news_details_subhead section:nth-child(1){
  font-size: 12px;
}
.news_details_subhead section:nth-child(1) a{
  cursor: pointer;
  color: #6398ed;
}
.news_details_subhead section:nth-child(1) span{
  padding-left: 20px;
}
.news_details_subhead section:nth-child(2){
  display: flex;
  align-items: center;
}
.news_details_subhead section:nth-child(2) span{
  padding-left: 5px;
  width: 22px;
  height: 22px;
}
.news_details_content p{
  font-size: 12px;
  color: #898989;
  line-height: 24px;
  padding: 20px 0;
}
.news_details_content img{
  max-width: 100%;
  height: auto;
}



