
.vheader{
  width: 100%;
  height: 30px;
  min-width: 1210px;
  border-bottom: 1px solid #ccc;
  background: #f1f1f1;
  color: #898989;
}
.vheader>div:first-child{
  width: 1210px;
  height: 30px;
  margin: auto;
}
.vheader div p{
  line-height: 30px;
}
.vheader div p:nth-child(1){
  float: left;
}
.vheader div p:nth-child(1) span:nth-child(3){
  color: #2990FF ;
  cursor: pointer;
    transition: .3s
}
.vheader div p:nth-child(1) span:nth-child(3):hover{
      color: #59a9ff
}
.vheader div p:nth-child(2){
  float: right;
}
.vheader div p:nth-child(2) a{
  color: #898989;
  padding-left: 10px;
  margin-left: 10px;
    transition: .3s;
    position: relative;
}
.vheader div p:nth-child(2) a:after{
      height: 10px;
      width: 1px;
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      margin-top: -5px;
      background: #ccc
}
.vheader div p:nth-child(2) a:hover{
      color: #0484FF
}
.windows_wrapper{
      z-index: 1000;
      background: rgba(0,0,0,.6)
}
.city-list{
      margin-top: 20px
}
.city-content b{
      font-size: 16px;
      font-weight: bold;
      margin-bottom: 10px;
      display: block;
      margin-top: 6px
}
.city-content{
      padding: 20px;
}
.city-content>div{
      padding: 10px
}
.city-hot{
      background: #fffbe7;
      border: 1px solid #ffe0c5
}
.city-content a{
      display: inline-block;
      padding: 5px 0;
      font-size: 14px;
      margin-right: 10px;
      color: #616161;
      transition: .3s;
}
.city-content a:hover{
      color: #006EFF
}
.city-list{
      background: #f7f7f7;
      border: 1px solid #e4e4e4
}



