
.recommend_box{
    width: 100%;
    height: auto;
    border: 1px solid #e4e0eb;
    margin-bottom: 20px;
}
.recommend_box_head{
    display: flex;
    height: 40px;
    padding: 0 20px;
    justify-content: space-between;
    align-items: center;
    background: #fcfcfc;
    border-bottom: 1px solid #e4e0eb;
}
.recommend_box_head span{
    color: #616161;
    font-size: 15px;
}
.recommend_box_head a{
    color: #b3b3b3;
    font-size: 13px;
      transition: .3s;
}
.recommend_box_head a:hover{
        color: #0079FF
}
.hot_news_item,.rec_organization_item,.rec_firm_item{
    margin: 0 20px;
    padding: 16px 0;
    border-bottom: 1px dashed #e4e0eb;
}
.hot_news_item a,.rec_organization_item a,.rec_firm_item a{
    display: flex;
    line-height: 22px;
    color: #898989;
      transition: .3s
}
.rec_organization_item>a:hover{
        color: #0079FF
}
.hot_news_item:last-child,.rec_organization_item:last-child,.rec_firm_item:last-child{
    border-bottom: 0;
}
.dot{
/*    font-size: 50px;*/
      display: inline-block;
      width: 6px;
      height: 6px;
      border-radius: 10px;
      background: #7E7E7E;
      overflow: hidden;
      position: absolute;
      left: 0;
      top: 8px
}
.hot_news_item>a{
        position: relative;
        padding-left: 12px;
        transition: .3s;
}
.hot_news_item>a:hover{
        color: #0079FF
}
.hot_news_item p,.rec_firm_item p{
    font-size: 14px;
      transition: .3s;
}
.rec_firm_item p:hover{
        color: #0079FF
}
.hot_news_item p span{
    color: #d6d6d6;
}
.rec_organization_item a img{
    width: 50px;
    height: 50px;
    padding-right: 10px;
}
.rec_firm_item{
    display: flex;
}



