
.main_nav_wrapper{
    width: 100%;
    height: 40px;
    border-bottom: 2px solid #0584FF;
    background: #ffffff;
}
.main_nav{
    display: flex;
    width: 1210px;
    height: 42px;
    margin: auto;
    justify-content: space-between;
    align-items: center;
    background: #0584FF;
    color: #fff;
    font-size: 15px;
}
.main_nav p{
    height: 42px;
}
.main_nav div:nth-child(1){
    width: 226px;
    height: 42px;
    line-height: 42px;
    cursor: pointer;
    background: #2990FF;
    position: relative;
    transition: .3s;
}
.main_nav div:nth-child(1):hover{
        background: #419cff
}
.main_nav div:nth-child(1) span:nth-child(1){
    float: left;
    padding-left: 20px;
}
.main_nav div:nth-child(1) span.icon-angle-down{
    height: 42px;
    line-height: 42px;
    font-size: 12px;
    float: right;
    padding-right: 20px;
      opacity: .5
}
.main_nav p:nth-child(2){
    flex-grow: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.main_nav p:nth-child(2) a{
    width: 112px;
    height: 42px;
    line-height: 42px;
    text-align: center;
    cursor: pointer;
    color: #fff;
    transition: .3s;
}
.main_nav p:nth-child(2) a:hover{
        background: rgba(255,255,255,.08)
}
.main_nav p:nth-child(2) i{
    height: 16px;
    width: 1px;
    background: #4598FC;
}
.main_nav p:nth-child(3){
    width: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
      transition: .3s
}
.main_nav p:nth-child(3):hover{
        background: #2890FF
}
.main_nav p:nth-child(3)>span:nth-child(1){
    padding-right: 7px;
    color:#fff
}
.main_nav p:nth-child(3)>a{
        color: #fff
}
.items_num{
    width: 15px;
    height: 15px;
    border-radius: 100%;
    background: #ff8a6e;
    margin-left: 5px;
    text-align: center;
    line-height: 15px;
    font-size: 14px;
}
.main_drop_down{
    position: absolute;
    width: 226px;
    height: 406px;
    left: 0;
    top: 42px;
    z-index: 9;
      background: rgba(0,0,0,.7)
}
.main_drop_down section{
    padding: 10px 20px 10px 20px;
/*    background: #5385d5;*/
    border-bottom: 1px solid rgba(255,255,255,.1);
      transition: .3s;
}
.main_drop_down section:hover{
        background: rgba(0,0,0,.4)
}
.main_drop_down section:last-child{
    border: 0;
}
.main_drop_down section p{
    display: flex;
    height: 30px;
    justify-content: flex-start;
    align-items: center;
    font-size: 14px;
}
.main_drop_down section p span:nth-child(2){
    padding-left: 10px;
      font-size: 14px
}
.main_drop_down section p span:nth-child(2)>a{
        font-size: 14px;
        font-weight: bold
}
.main_drop_down section a{
    float: left;
    line-height: 20px;
    font-size: 12px;
    padding-right: 14px;
    color: #fff;
      transition: .3s;
}
.main_drop_down section a:hover{
        color: #FFFF63
}
.slide-fade-enter-active {
    transition: all .3s ease;
}
.slide-fade-leave-active {
    transition: all .3s;
    opacity: 0;
}
.slide-fade-enter{
    transform: translateY(-10px);
    opacity: 0;
}



