
.firm_detail_head_wrapper{
    width: 100%;
    height: 198px;
    padding: 20px 0;
    background: #f5f5f5;
}
.firm_detail_head{
    width: 1208px;
    height: 194px;
    margin: auto;
    border: 1px solid #e5e7ed;
    background: #fff;
}
.firm_detail_head{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.firm_detail_head img{
    width: 132px;
    height: 132px;
    border: 1px solid #e5e7ed;
    margin: 0 20px;
}
.firm_detail_head section h1{
    display: flex;
    height: 40px;
    justify-content: flex-start;
    align-items: center;
    font-size: 20px;
    color: #616161;
    font-weight: 600;
}
.firm_detail_head section h1 a{
    font-size: 13px;
    color: #6398ed;
    font-weight: normal;
    padding-left: 20px;
}
.firm_detail_head section h1 a span{
    font-size: 16px;
    padding-right: 5px;
}
.firm_detail_head section p{
    display: flex;
    height: 30px;
    justify-content: flex-start;
    align-items: center;
    font-size: 15px;
    color: #b3b3b3;
}
.firm_detail_head section p span:nth-child(2n){
    padding: 0 25px 0 10px;
}
.firm_detail_head section p span a{
    color: #6398ed;
}
.firm_detail_head section div{
    display: flex;
    height: 40px;
    justify-content: flex-start;
    align-items: center;
}
.firm_detail_head section div span{
    width: 22px;
    height: 22px;
    padding-left: 10px;
}
.firm_detail_head section div span img{
    width: 22px;
    height: 22px;
    border: 0;
    margin: 0;
}
.firm_detail_content{
    padding: 30px 20px;
}
.organ_head_infor{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 962px;
    height: 300px;
    border: 1px solid #eef0f6;
    margin-bottom: 20px;
}
.organ_head_item{
    display: flex;
    width: 90%;
    height: 50px;
    font-size: 14px;
    justify-content: flex-start;
    align-items: center;
    color: #898989;
}
.organ_head_name{
    width: 100px;
    font-weight: bold;
    color: #616161;
}
.organ_head_item div span{
    padding: 5px 10px;
    border: 1px solid #eef0f6;
    margin-right: 20px;
}
.organ_head_item .money{
    font-size: 16px;
    font-weight: bold;
    color: orange;
}
.btn{
    height: 30px;
    border: 1px solid #eef0f6;
    border-radius: 5px;
    text-align: center;
    line-height: 30px;
}
.btn_buy{
    width: 100px;
    background: #6398ed;
    color: #fff;
}
.btn_add{
    width: 150px;
    background: #d6d6d6;
    color: #616161;
    margin-left: 20px;
}
.orgain_content_right{
    float: right;
}
#mainLeft{
  	border: none;
}
.orgain_content_left{
    float: left;
    border: 1px solid #e5e7ed;
}
.top{
  	width: 100%;
/*  	height: 264px;*/
  	background: #f8f8f8;
  	box-sizing: border-box;
  	border: 1px solid transparent;
  	border-bottom:1px solid #e5e7ed ;
      padding-bottom: 10px
}
.top-icon{
  	width: 120px;
  	height: 120px;
  	padding: 5px;
  	background: #fff;
  	border: 1px solid #e5e7ed;
  	margin: 20px auto 10px ;
}
.top-icon img{
  	width: 100%;
  	height: 100%;
}
.top-company{
  	width: 130px;
  	margin: 0 auto;
  	text-align: center;
  	font-size: 16px;
		line-height: 23px;
}
.top-tab{
  	width: 180px;
  	margin: 10px auto;
}
.top-tab div{
  	width: 80px;
  	height: 30px;
  	line-height: 30px;
		text-align: center;
  	border-radius: 3px;
}
.top-tab div img{
  	vertical-align: -3px;
  	margin-right: 3px;
}
.communication{
  	float: left;
  	border:1px solid #d4e1f5;
}
.focus{
  	float: right;
		border:1px solid #e5e7ed;
}
.mid-list{
  	padding: 0 20px;
  	overflow: hidden;
  	border-bottom: 1px solid #e5e7ed;
}
.mid-list li{
  	line-height: 30px;
  	font-size: 14px;
  	color: #666;
}
.mid-list li:first-child{
  	font-size: 12px;
  	font-weight:bold ;
}
.mid-list li img{
  	vertical-align: -4px;
  	margin-right: 10px;
}
.cborder{
  	border: none;
}
.scmessa{
  	float: right;
  	width: 160px;
  	margin-top: 6px;
  	line-height: 20px;
}
.map{
  	margin: 10px 0;
  	text-align: center;
}
.protect{
  	margin-top: 20px;
}
.protect-title{
  	width: 100%;
  	height: 35px;
  	line-height: 35px;
  	background: #fcfcfc;
  	border-bottom:1px solid #e5e7ed ;
  	box-sizing: border-box;
  	padding-left: 20px;
  	font-size: 15px;
  	color: #666;
}
.protect-list{
  	padding: 0px 20px;
}
.protect-list li{
  	float: left;
  	width: 90px;
  	text-align: center;
  	margin-top: 15px;
		margin-bottom: 15px;
}
.protect-list li p{
  	margin-top:5px ;
  	font-size: 16px;
  	color: #999;
}
.service-active{
    background-color: #659AEA;
    color: white;
}
.organization-claim{
        text-align: center;
        font-size: 14px;
        color: #0079FF;
        margin-top: 10px;
        transition: .3s;
        cursor: pointer;
        line-height: 24px
}
.organization-claim:hover{
        color: #3695ff
}
.organization-claim>span{
        margin-right: 6px;
}



