
.firm_list li{
  display: flex;
  height: 170px;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #e5e7ed;
}
.firm_list li img{
  width: 110px;
  height: 110px;
  border: 1px solid #e5e7ed;
  margin: 0 20px;
}
.firm_list li section{
  flex-grow: 1;
}
.firm_list li section a{
  display: block;
  font-size: 16px;
  color: #616161;
  line-height: 32px;
}
.firm_list li section span{
  display: block;
  font-size: 14px;
  color: #b3b3b3;
  line-height: 25px;
}
.firm_list li p{
  width: 105px;
  font-size: 14px;
  color: #b3b3b3;
  text-align: center;
}
.firm_list_tabs{
  display: flex;
  height: 50px;
  justify-content: flex-start;
  align-items: center;
}
.firm_list_tabs span{
  padding: 0px 15px;
  border: 1px solid #e5e7ed;
  margin-right: 10px;
}



