
.cartSuccess{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 500px;
    margin: 30px 0;
    border: 1px solid #eef0f6;
}
.success{
    display: flex;
    justify-content: center;
    align-items: flex-start;
}
.icon-ok{
    font-size: 65px;
    color: #87ce76;
}
.infor{
    display: flex;
    flex-direction: column;
}
.font1{
    font-size: 22px;
    color:#616161;
    padding-top: 10px;
}
.font2{
    font-size: 15px;
    color: #898989;
    padding: 10px 0;
}
.font3{
    font-size: 15px;
    color: #b3b3b3;
}
.btns{
    display: flex;
    justify-content: center;
    align-items: center;
}
.btns a{
    font-size: 16px;
    padding: 50px 20px;
    color: #6398ed;
}



