
.cartSuccess{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 500px;
    margin: 30px 0;
    border: 1px solid #eef0f6;
}
.success{
    display: flex;
    justify-content: center;
    align-items: flex-start;
}
.icon-ok{
    font-size: 65px;
    color: #87ce76;
}
.infor{
    display: flex;
    flex-direction: column;
}
.font1{
    font-size: 22px;
    color:#616161;
    padding-top: 10px;
}
.font2{
    font-size: 15px;
    color: #898989;
    padding: 10px 0;
}
.font3{
    font-size: 15px;
    color: #b3b3b3;
}
.btns{
    display: flex;
    justify-content: center;
    align-items: center;
}
.btns a{
    font-size: 16px;
    padding: 50px 20px;
    color: #6398ed;
}
.pay-dialog-cont-btn{
      text-align: center
}
.pay-dialog-cont-btn>*{
      width: 210px;
      margin: 0 5px
}
.pay-dialog-cont{
      margin-top: 30px;
      border-top: 1px solid #ECEDF0;
      padding-top: 30px
}
.pay-dialog-cont>b{
      font-size: 16px;
      color: #616161;
      display: block;
      padding-bottom: 10px
}
.pay-dialog-cont>b>i{
      color: #0079FF;
      margin-right: 5px
}
.pay-dialog-cont>p{
      line-height: 22px;
      font-size: 14px;
      color: #898989;
      padding: 3px 0
}
.pay-dialog-cont>p>a{
      color: #0079FF;
      transition: .3s;
}
.pay-dialog-cont>p>a:hover{
      color: #3193ff
}
.order-dialog{
      font-size: 26px;
      text-align: center;
      color: #898989;
      line-height: 26px;
      padding-bottom: 0px
}
.order-dialog>.icon-close{
      color: #FF8A6E;
      margin-right: 10px;
      vertical-align: middle
}
.order-dialog>.icon-ok{
      color: #86CD76;
      margin-right: 10px;
      vertical-align: middle
}



