
.firm_detail_head_wrapper{
  width: 100%;
  height: 198px;
  padding: 20px 0;
  background: #f5f5f5;
}
.firm_detail_head{
  width: 1208px;
  height: 194px;
  margin: auto;
  border: 1px solid #e5e7ed;
  background: #fff;
}
.firm_detail_head{
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.firm_detail_head img{
  width: 132px;
  height: 132px;
  border: 1px solid #e5e7ed;
  margin: 0 20px;
}
.firm_detail_head section h1{
  display: flex;
  height: 40px;
  justify-content: flex-start;
  align-items: center;
  font-size: 20px;
  color: #616161;
  font-weight: 600;
}
.firm_detail_head section h1 a{
  font-size: 13px;
  color: #6398ed;
  font-weight: normal;
  padding-left: 20px;
}
.firm_detail_head section h1 a span{
  font-size: 16px;
  padding-right: 5px;
}
.firm_detail_head section p{
  display: flex;
  height: 30px;
  justify-content: flex-start;
  align-items: center;
  font-size: 15px;
  color: #b3b3b3;
}
.firm_detail_head section p span:nth-child(2n){
  padding: 0 25px 0 10px;
}
.firm_detail_head section p span a{
  color: #6398ed;
}
.firm_detail_head section div{
  display: flex;
  height: 40px;
  justify-content: flex-start;
  align-items: center;
}
.firm_detail_head section div span{
  width: 22px;
  height: 22px;
  padding-left: 10px;
}
.firm_detail_head section div span img{
  width: 22px;
  height: 22px;
  border: 0;
  margin: 0;
}
.firm_detail_content{
  padding: 30px 20px;
}



