
.main_wrapper_wrapper{
  background:#fff;
}
.main_header{
  display: flex;
  width: 1210px;
  height: 82px;
  justify-content: space-between;
  align-items: center;
}
.head_select{
  display: flex;
  width: 542px;
  height: 32px;
  justify-content: space-between;
  align-items: center;
  border: 2px solid #0484FF;
}
.head_select select{
  width: 112px;
  height: 32px;
  border: none;
  font-size: 13px;
  color: #b3b3b3;
}
.head_select input{
  flex-grow: 1;
  height: 32px;
  border: none;
  padding: 0;
  padding-left: 15px;
  border-left: 1px solid #f1f1f1;
  font-size: 13px;
}
.head_select_but{
  width: 84px;
  height: 32px;
  text-align: center;
  line-height: 32px;
  background: #0484FF;
  color: #fff;
  font-size: 14px;
    cursor: pointer;
    transition: .3s;
}
.head_select_but:hover{
      background: #429BFF
}
.main_unlogin .icon-user{
  color: #d6d6d6;
  padding: 5px;
  margin-right: 5px;
  border: 1px solid #d6d6d6;
  border-radius: 100%;
}
.main_unlogin a{
  color: #616161;
}
.main_unlogin a:nth-child(2){
  padding-right: 10px;
  margin-right: 5px;
  border-right: 1px solid #d6d6d6;
}
.main_unlogin a:nth-child(3){
  padding-right: 10px;
  margin-right: 5px;
  border-right: 1px solid #d6d6d6;
}



