
.labelBox {
  display: flex;
  width: 100%;
  height: 128px;
  padding-top: 428px;
  justify-content: space-between;
  align-items: center;
}
.labels {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 228px;
  height: 128px;
  padding: 0 30px;
  background: #ffffff;
  border: 1px solid #efefef;
}
.labels p {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.labels p span:nth-child(1) {
  font-size: 20px;
  color: #4a4a4a;
}
.labels p span:nth-child(2) {
  padding-top: 12px;
  font-size: 14px;
  color: #bababa;
}
.labels img {
  display: block;
  width: 58px;
  height: auto;
}
.messageBox{
	width: 100%;
	height: 344px;
	margin-top: 20px;
}
.messageCentent{
	position: absolute;
	width: 100%;
	height: 344px;
	background: #ffffff;
	left: 0;
	min-width: 1210px;
}
.messageCententBox{
	width: 1210px;
	margin: 20px auto;
}



