
.cartList_title{
    display: flex;
    width: 96%;
    height: 45px;
    padding: 0 2%;
    justify-content: space-between;
    align-items: center;
    background: #f9fafc;
    border: 1px solid #eef0f6;
}
.title_head{
    width: 200px;
}
.cartList_title span,.order_name{
    color: #898989;
    font-size: 16px;
}
.ring{
    display: inline-block;
    width: 16px;
    height: 16px;
    border: 1px solid #eef0f6;
    border-radius: 100%;
    background: #ffffff;
}
.ring.active{
    background: #72CF6F;
}
.cartList_title .label,.order_name{
    padding: 0 20px;
}
.cartList_title .cartList_title_do{
    padding-right: 50px;
}
.cartList_order_title{
    display: flex;
    height: 40px;
    padding-top: 10px;
    justify-content: flex-start;
    align-items: center;
    font-size: 16px;
    color: #898989;
}
.cartList_orders li{
    display: flex;
    height: 60px;
    width: 96%;
    padding: 0 2%;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    color: #898989;
    border: 1px solid #eef0f6;
    background: #f9fafc;
}
.pay_money{
    color: #ff8a6e;
}
.handle{
    padding-right: 50px;
}
/*.cartList_orders li:last-child{
    border-top: 0;
}*/
.gongsi{
    padding-left: 10px;
}
.cartList_footer{
    width: 98%;
    padding-right: 0;
    justify-content: space-between;
    font-size: 16px;
    color: #898989;
    margin-top: 20px;
}
.cartList_footer span i{
    color: #ff8a6e;
}
span.go_pay{
    display: inline-block;
    width: 128px;
    height: 45px;
    text-align: center;
    line-height: 45px;
    background: #6398ed;
    color: #ffffff;
}



