
.invoice_table .money{
  color: #ff8a6e;
}
.manage_list_common.manage_list_invoiceapply li p:nth-child(1){
  padding-left: 35px;
  width: 175px;
}
.manage_list_invoiceapply li section .types{
  border: 1px solid #e5e6ed;
  padding: 10px;
  color: #898989;
  margin-right: 10px;
  cursor: pointer;
}
.manage_list_invoiceapply li section .types.active{
  border: 1px solid #6389ed;
  background: #6389ed;
  color: #fff;
}
.title_invoice{
  padding-top: 40px;
  padding-bottom: 25px;
  border-bottom: 1px solid #e5e6ed;
  color: #616161;
  font-size: 14px;
  font-weight: 600;
}
.consignee_address{
  display: flex;
  height: 70px;
  justify-content: flex-start;
  align-items: center;
  padding-left: 20px;
  border-bottom: 1px solid #e5e6ed;
}
.consignee_address span{
  padding-right: 10px;
  color: #898989;
  font-size: 14px;
}
.consignee_address span:nth-child(1){
  font-size: 15px;
}
.consignee_address span:nth-child(2){
  font-weight: 600;
}
.manage_list_invoiceapply li p.des_1{
  width: 600px;
  font-size: 12px;
  color: #898989;
  line-height: 30px;
}
.invoiceapply_btn_box{
  padding: 30px 0;
}
.invoiceapply_cancel_btn,.invoiceapply_submit_btn{
  width: 160px;
  height: 45px;
  line-height: 45px; 
  margin: 0 10px;
}
span.change_address{
  color: #6389ed;
  cursor: pointer;
}
.file-select-box{
  position:absolute;
  width: 100px;
  height: 100px;
  overflow: hidden;
  left:505px;
}
.file-select-box input{
  padding-bottom: 60px;
  opacity: 0.1;
}



