
.approve_table{
  width: 100%;
  font-size: 14px
}
.approve_table td{
  height: 45px;
  vertical-align:middle;
  text-align: left;
  padding-left: 10px;
  border: 1px solid #eef0f6;
}
.approve_table td.long_td{
  width: 400px;
}
.passed{
  color:#64CA60;
}
.nopassed{
  color: #adadad;
}
.passing{
  color: #F8B800;
}
.failpass{
  color: #FF0000;
}
.approve-state{
        width: 160px;
        display: inline-block
}



