
.help_select{
  display: flex;
  width: 550px;
  height: 32px;
  border: 2px solid #0065FF;
  margin: 30px auto 35px auto;
}
.help_select input{
  flex-grow: 1;
  border: 0;
  padding-left: 10px;
}
.help_select_btn{
  width: 80px;
  height: 32px;
  background: #0065FF;
  color: #fff;
  font-size: 15px;
  text-align: center;
  line-height: 32px;
    transition: .3s;
    cursor: pointer
}
.help_select_btn:hover{
      background: #3b8aff
}
.company-search-no{
      text-align: center;
      font-size: 14px;
      padding: 30px 0
}
.company-search-no>span{
      color: #AAAAAA;
}



