
.carthot{
    padding-bottom: 10px;
    border-bottom: 1px solid #eef0f6;
    color: #898989;
}
.cartDetail_title{
    display: flex;
    width: 98%;
    margin: auto;
    height: 60px;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #eef0f6;
}
.cartDetail_title_name{
    font-size: 13px;
    font-weight: bold;
    color: #616161;
}
.cartDetail_title_btn a{
    color:#8aaff1;
}
.address{
    width: 88%;
    height: 100px;
    padding: 0 5%;
    margin: auto;
    font-size: 16px;
    color: #898989;
    line-height: 80px;
    border-bottom: 1px solid #eef0f6;
}
.address span{
    padding: 0 10px;
}
.pay_way{
    width: 88%;
    padding: 2% 5%;
    border-bottom: 1px solid #eef0f6;
}
.pay_way img{
    display: inline-block;
    margin-right: 20px;
    border: 1px solid #eef0f6;
}
.gongsiname{
    width: 98%;
    margin: auto;
    font-size: 16px;
    color: #898989;
    line-height: 40px;
}
.cartlists li{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    width: 92%;
    padding: 0 3%;
    font-size: 16px;
    color: #898989;
    background: #f9fafc;
    margin: auto;
    border: 1px solid #eef0f6;
}
.money{
    color: #ff8a6e;
}
.cartList_footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 40px auto;
    height: 45px;
    width: 96%;
    padding: 0 2%;
    padding-right: 0; 
    background: #f9fafc;
    border: 1px solid #eef0f6;
}
.cartList_footer span{
    font-size: 16px;
    color: #898989;
}
.cartList_footer span i{
    color: #ff8a6e;
}
span.go_pay{
        display: inline-block;
        width: 128px;
        height: 45px;
        text-align: center;
        line-height: 45px;
        background: #6398ed;
        color: #ffffff;
}
.pay_way>.active{
    border: 1px solid #559CF1;
}







