
.firm_label{
  display: inline-block;
  width: 95px;
  height: 28px;
  border: 1px solid #e5e7ed;
  line-height: 28px;
  text-align: center;
  cursor: pointer;
  font-size: 14px;
  color: #b3b3b3;
  border-radius: 4px;
  margin-right: 10px;
}
.table_head{
  display: flex;
  height: 48px;
  padding-top: 10px;
  justify-content: space-between;
  align-items: center;
  font-size: 0;
}
.table_head section span:nth-child(1){
  font-size: 14px;
  color: #616161;
  font-weight: 600;
}
.table_head section span:nth-child(2){
  display: inline-block;
  height: 14px;
  line-height: 14px;
  font-size: 10px;
  color: #fff;
  padding: 0px 4px;
  background: #9cb3c2;
  border-radius: 4px;
  margin-left: 12px;
}
.table_head section select{
  width: 75px;
  height: 23px;
  font-size: 12px;
  background: #f9fafc;
  color: #898989;
  border: 1px solid #e5e7ed;
  border-radius: 4px;
  margin-left: 10px;
}
.table_box table tr th,.table_box table tr td{
  color: #898989;
  border: 1px solid #e5e7ed;
  text-align: left;
  padding-left: 20px;
  vertical-align:middle;
}
.table_box table tr th{
  height: 38px;
  background: #f9fafc;
}
.table_box table tr td{
  height: 75px;
}
.table_box table tr td img{
  width: 56px;
  height: 34px;
}
.table_box table tr td a{
  color: #6398ed;
}



