
.news_list{
  padding-bottom: 30px;
}
.news_item{
  display: flex;
  justify-content: flex-start;  /*改动, 靠左对齐*/
  align-items: center;
  padding: 20px 0;
  margin: 0 20px;
  border-bottom: 1px dashed #e5e7ed;
}
.news_item img{
  min-width: 160px;
  height: 106px;
}
.news_item .news_infor{
  display: flex;
  height: 106px;
  width: 100%;
  padding-left: 20px;
  flex-direction: column;
  justify-content: space-around;
}
.news_item .news_infor a{
  cursor: pointer;
}
.news_item .news_infor .news_tit{
  font-size: 18px;
  color: #616161;
    transition: .3s;
}
.news_item .news_infor .news_tit:hover{
      color: #0079FF
}
.news_item .news_infor .news_des{
  font-size: 14px;
  color: #b3b3b3;
  line-height: 24px;
}
.news_item .news_infor .news_time{
  font-size: 11px;
  color: #d6d6d6;
  align-self: flex-end;
}






