
.messageList li{
  display: flex;
  width: 880px;
  height: 90px;
  padding: 0 20px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  border: 1px solid #eef0f6;
  background: #f9fafc;
}
.messageList li section:nth-child(1){
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.messageList li section:nth-child(2){
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.messageTitle{
  font-size: 14px;
  font-weight: bold;
  color: #616161;
}
.messageTime{
  font-size: 13px;
  color: #b3b3b3;
}
.messageInfor{
  font-size: 14px;
  color: #898989;
  line-height: 30px;
}
.messageBtn{
  padding: 5px 20px;
  border-radius: 3px;
  background: #fff;
    cursor: pointer;
    transition: .3s
}
.messageBtnDetail{
  border: 1px solid #72CE6F;
  color: #72CE6F;
}
.messageBtnDetail:hover{
        background: #72CE6F;
        color: #fff
}
.messageBtnDelet{
  border: 1px solid #E5E7EE;
  color: #898989;
  margin-left: 20px;
    transition: .3s;
}
.messageBtnDelet:hover{
        color: #FF826B
}
.windows-cont{
        width: 550px
}
.windows_content>*{
        height: auto!important;
        padding: 6px 0;
        align-items:baseline!important;
        line-height: 20px
}
.windows_btn{
        height: auto;
        padding-bottom: 30px
}
.windows_btn_del{
        color: #FF826B
}



