
.loginNav_wrapper{
  width: 100%;
  border-bottom: 1px solid #f5f5f5;
}
.main_header{
    display: flex;
    width: 1210px;
    height: 82px;
    justify-content: space-between;
    align-items: center;
}
.main_unlogin .icon-user{
    color: #d6d6d6;
    padding: 5px;
    margin-right: 5px;
    border: 1px solid #d6d6d6;
    border-radius: 100%;
}
.main_unlogin a{
    color: #616161;
}
.main_unlogin a:nth-child(2){
    padding-right: 10px;
    margin-right: 5px;
    border-right: 1px solid #d6d6d6;
}
.loginNav_wrapper .main_header{
    height: 62px;
}
.loginNav_wrapper .main_header img{
    width: 162px;
    height: 45px;
}



